/* Основные стили для хедера */
header {
  background-color: white;
  color: black;
  padding-top: 20px;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  flex-wrap: wrap; /* Allows items to wrap to new lines if necessary */
}

/* Стили для логотипа */
.logo-container {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}

.logo {
  height: 60px;
}

/* Стили для навигации */
.navigation1 {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

.nav-menu {
  list-style: none;
  display: flex;
  flex-wrap: wrap; /* Allows menu items to wrap */
  justify-content: center;
  gap: 30px;
  padding: 0;
  margin: 0;
}

.nav-menu li {
  margin: 0;
}

.nav-menu li button {
  background: none;
  border: none;
  color: #2c3e50;
  text-decoration: none;
  font-weight: 600;
  font-size: 26px;
  transition: color 0.3s ease;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
  letter-spacing: -0.5px;
}

.nav-menu li button:hover {
  color: darkslategray;
}

.nav-menu li button::after {
  content: '';
  position: absolute;
  width: 0;
  height: 3px;
  background: #2c3e50;
  left: 0;
  bottom: -5px;
  transition: width 0.3s ease;
}

.nav-menu li button:hover::after {
  width: 100%;
}

/* Стили для кнопки */
.header-download-btn {
  display: flex;
  align-items: center;
}

.header-download-btn button {
  background-color: #41930b;
  color: white;
  border: none;
  padding: 12px 25px;
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.header-download-btn button:hover {
  background-color: #57d100;
  transform: scale(1.04);
}

/* Адаптивность */

@media (max-width: 1624px) {
  .nav-menu li button {
    font-size: 20px;
  }
  .nav-menu {
    gap: 10px;
  }
}

@media (max-width: 1200px) {
  .nav-menu li button {
    font-size: 20px;
  }
}

@media (max-width: 992px) {
  .header-container {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .navigation1 {
    order: 2; /* Навигация во второй строке */
    width: 100%; /* Полная ширина */
    justify-content: center; /* Центрирование навигации */
  }

  .header-download-btn {
    order: 3; /* Кнопка в третьей строке */
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .logo-container {
    order: 1; /* Логотип в первой строке */
  }
}

@media (max-width: 768px) {
  .header-container {
    gap: 20px;
  }

  .navigation1 {
    order: 2;
    width: 100%;
  }

  .nav-menu {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .header-download-btn {
    order: 3;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .logo-container {
    order: 1;
  }
}

@media (max-width: 576px) {
  .nav-menu {
    gap: 5px;
  }

  .nav-menu li button {
    font-size: 18px;
  }

  .header-download-btn button {
    font-size: 14px;
    padding: 10px 20px;
  }
}