.Services {
  padding: 20px 0;
  text-align: center;
  margin-bottom: 20px;
  border-bottom: 2px solid #ddd;
  font-size: 46px;
  background-color: #f4f4f4;
}

.Services h2 {
  margin: 0;
  font-size: 46px !important;
  margin-bottom: 40px;
  background: #2c3e50;
  color: white;
  padding: 10px;
}

.rectangles {
  display: flex;
  justify-content: center;
  gap: 30px; /* Adjusted for better spacing */
  flex-wrap: wrap;
}

.rectangle {
  width: calc(25% - 30px); /* Adjust width for four columns with gap */
  max-width: 400px; /* Maximum width for larger screens */
  height: 200px;
  background-color: white;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 36px;
  font-weight: 400;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: #333;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
  margin-bottom: 30px; /* Space between rectangles */
}

.rectangle:hover {
  transform: scale(1.05);
  background-color: #2c3e50;
  color: white;
}

/* Адаптивность */
@media (max-width: 1824px) {
  .Services {
    font-size: 34px; /* Меньший размер шрифта */
  }
  .Services h2 {
    font-size: 34px !important; /* Меньший размер заголовка */
  }
  .rectangle {
    max-width: 550px;
    font-size: 24px; /* Меньший размер шрифта прямоугольника */
    width: calc(50% - 30px); /* Two columns with gap */
  }
}

@media (max-width: 1324px) {
  .Services {
    font-size: 32px; /* Меньший размер шрифта */
  }
  .Services h2 {
    font-size: 32px !important; /* Меньший размер заголовка */
  }
  .rectangle {
    font-size: 22px; /* Меньший размер шрифта прямоугольника */
    width: calc(50% - 30px); /* Two columns with gap */
  }
}

@media (max-width: 1024px) {
  .Services {
    font-size: 26px; /* Меньший размер шрифта */
  }
  .Services h2 {
    font-size: 26px !important; /* Меньший размер заголовка */
  }
  .rectangle {
    font-size: 20px; /* Меньший размер шрифта прямоугольника */
    width: calc(50% - 30px); /* Two columns with gap */
  }
}

@media (max-width: 768px) {
  .Services {
    font-size: 24px; /* Меньший размер шрифта */
  }
  .Services h2 {
    font-size: 24px !important; /* Меньший размер заголовка */
  }
  .rectangle {
    font-size: 20px; /* Меньший размер шрифта прямоугольника */
    width: calc(50% - 30px); /* Two columns with gap */
  }
}

@media (max-width: 480px) {
  .Services {
    font-size: 20px; /* Меньший размер шрифта */
  }
  .Services h2 {
    font-size: 20px !important; /* Меньший размер заголовка */
  }
  .rectangle {
    font-size: 20px; /* Меньший размер шрифта прямоугольника */
    width: calc(100% - 30px); /* Full width with gap */
    max-height: 20% !important;
  }
}